@media (min-width: 320px) and (max-width: 767.98px) {
  .main {
    font-size: 20px;
    background-color: rgb(47, 60, 208);
  }
}
@media (min-width: 768px) and (max-width: 1439.98px) {
  .main {
    font-size: 50px;
    background-color: rgb(227, 169, 22);
  }
}
@media (min-width: 1440px) {
  .main {
    font-size: 100px;
    background-color: brown;
  }
}
